



















import tagListModel from "@/models/tagListModel";
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
@Component
export default class Tags extends Vue {
  @Prop(Array) readonly dataSource: [] | undefined;
  selectTags: { id: string; name: string }[] = [];
  toggle(tag: { id: string; name: string }): void {
    const index = this.selectTags.indexOf(tag);
    if (index >= 0) {
      this.selectTags.splice(index, 1);
    } else {
      this.selectTags.push(tag);
    }
    this.$emit("update:value", this.selectTags);
  }
  // @Watch("dataSource")
  // updateValue(newValue: string[]): void {
  //   this.$emit("update:value", newValue);
  // }
  creat(): void {
    const name = window.prompt("请输入标签名");
    if (name) {
      const result = tagListModel.create(name);
      if (result === "seccess") {
        window.alert("创建成功");
      } else if (result === "duplicated") {
        window.alert("标签名重复");
      }
    } else {
      window.alert("标签名不能为空");
    }

    // if (name === "") {
    //   window.alert("标签名不能为空");
    // } else {
    //   if (this.dataSource && this.dataSource.indexOf(name as string) < 0) {
    //     // this.dataSource.push(name as string) 外部数据不可修改
    //     this.$emit("update:dataSource", [...this.dataSource, name]);

    //   } else {
    //     window.alert("已有相同标签名");
    //   }
    // }
  }
}
